import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import {
  ShowModalKondisiPinjaman,
  showModal,
  hideModal,
  getKondisiPinjaman,
} from "../../../actions/datamaster_action.jsx";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import {
  NotifSucces,
  ToastNotification,
} from "../../../components/helpers/notification.jsx";
import { deleteData, postData, putData } from "../../../config/axios.jsx";
import { reset } from "redux-form";
import { ErrorHandling } from "../../../components/helpers/function.jsx";
import Tabel from "../../../components/helpers/tabel.jsx";
import FormModalKondisiPinjaman from "./FormModalKondisiPinjaman.jsx";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";

const maptostate = (state) => {
  return {
    DataKondisiPinjaman: state.datamaster.getKondisiPinjaman,
    hideModal: state.datamaster.modalDialog,
  };
};
const hapusDataKondisiPinjaman = (params, dispatch) => {
  Swal.fire({
    // title: "Anda Yakin !!",
    // text: "Ingin Menghapus Data Ini ?",
    html:
      "Apakah Anda Yakin Ingin " +
      "Menghapus " +
      "<h1><b>Nama Kondisi " +
      params +
      "</b> ?</h1>",
    icon: "warning",
    position: "top-center",
    cancelButtonText: "Tidak",
    showCancelButton: true,
    confirmButtonText: "OK",
    showConfirmButton: true,
  }).then((result) => {
    if (result.isConfirmed) {
      deleteData("parapinjaman/delete/by-kode-kondisi/" + params, {}).then(
        () => {
          NotifSucces("Data Berhasil Di Hapus").then(() =>
            dispatch(getKondisiPinjaman())
          );
        }
      );
    }
  });
};
class MasterKondisiPinjaman extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      isLoading: false,
      modalDialog: false,
      columns: [
        {
          dataField: "kondisi_barang",
          text: "Nama Kondisi",
          sort: true,
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            let dataEdit = {
              kondisi_barang: row.kondisi_barang,
            };
             
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => this.formEditKondisiPinjaman(dataEdit)}
                    className="btn btn-primary mr-3"
                  >
                    <i className="fa fa-edit"></i>
                  </button>
                  <button
                    onClick={() =>
                      hapusDataKondisiPinjaman(
                        row.kondisi_barang,
                        this.props.dispatch
                      )
                    }
                    className="btn btn-danger"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    this.props.dispatch(getKondisiPinjaman());
  }
  handleSubmit(data) {
    this.setState({ isLoading: true });
    let data_edit = {
      kondisi_barang: data.kondisi_barang,
    };
    let data_post = {
      kondisi_barang: data.kondisi_barang,
    };
    this.state.isEdit
      ? putData(
          "parapinjaman/edit/by-kode-kondisi/" + data.kondisi_barang_lama,
          data_edit
        )
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => ToastNotification("success", "Data Berhasil Diedit"))
          .then(() => {
            this.props.dispatch(getKondisiPinjaman());
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .then(() => {
            this.props.dispatch(reset("ModalKondisiPinjaman"));
          })
          .catch((error) => this.ErrorHandling(error))
      : postData("parapinjaman/add", data_post)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => {
            ToastNotification("success", "Data Berhasil Disimpan");
          })
          .then(() => {
            this.props.dispatch(getKondisiPinjaman());
          })
          .then(() => {
            this.props.dispatch(reset("ModalKondisiPinjaman"));
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .catch((error) =>
            ErrorHandling(
              error,
              "parapinjaman/reactive/by-kode-kondisi/",
              data.kondisi_barang,
              data_post,
              this.props.dispatch,
              getKondisiPinjaman(),
              hideModal(),
              "ModalKondisiPinjaman"
            ).then(() => {
              this.setState({ isLoading: false });
            })
          );
  }

  formEditKondisiPinjaman(dataEdit) {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalKondisiPinjaman(dataEdit));
    this.setState({
      isEdit: true,
    });
  }
  ShowModalKondisiPinjaman() {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalKondisiPinjaman(false));
    this.setState({
      isEdit: false,
    });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Data Master</Link>
          </li>
          <li className="breadcrumb-item active">Master Kondisi Pinjaman</li>
        </ol>
        <h1 className="page-header">Master Kondisi Pinjaman </h1>
        <Panel>
          <PanelHeader>Master Kondisi Pinjaman</PanelHeader>
          <br />

          {/* Master Kondisi Pinjaman */}
          <div className="container">
            <Tabel
              keyField="kondisi"
              tambahData={true}
              handleClick={() => this.ShowModalKondisiPinjaman()}
              exportCsv={true}
              data={this.props.DataKondisiPinjaman}
              columns={this.state.columns}
              empty={this.props.DataKondisiPinjaman}
              textEmpty="Data Kondisi Pinjaman Kosong"
            />
          </div>
          <br />
          {/* End Master Pinjaman */}
          <ModalGlobal
            title={
              this.state.isEdit
                ? "Edit Data Kondisi Pinjaman"
                : "Tambah Data Kondisi Pinjaman"
            }
            size="P"
            content={
              <FormModalKondisiPinjaman
                isEdit={this.state.isEdit}
                isLoading={this.state.isLoading}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            }
          />

          {/* End Tambah Master Kondisi Pinjaman  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(MasterKondisiPinjaman);
