import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { hideModal } from "../../../actions/datamaster_action";
import { ReanderField } from "../../../components/helpers/field";
import ValidasiMasterKondisiPinjaman from "../../../Validasi/ValidasiMasterKondisiPinjaman.jsx";

const maptostate = (state) => {
  if (state.datamaster.ShowModalKondisiPinjaman !== null) {
    return {
      initialValues: {
        kondisi_barang_lama:
          state.datamaster.ShowModalKondisiPinjaman.kondisi_barang,
        kondisi_barang:
          state.datamaster.ShowModalKondisiPinjaman.kondisi_barang,
      },
    };
  }
};
class FormModalKondisiPinjaman extends Component {
  state = {
    aktif: false,
  };

  componentDidMount() {
    setTimeout(() => {
      document.getElementById("kondisi_barang").focus();
    }, 100);
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <input type="hidden" name="kondisi_barang_lama" />
        <Field
          id="kondisi_barang"
          tabIndex="1"
          name="kondisi_barang"
          component={ReanderField}
          type="text"
          label=" Kondisi Pinjaman"
          onBlur={() => this.setState({ aktif: true })}
          placeholder="Masukan  Kondisi Pinjaman"
        />
        <div className="row">
          <div className="col-12 text-right">
            <button
              className="btn btn-white"
              onClick={() => this.props.dispatch(hideModal())}
              type="button"
              disabled={this.props.isLoading}
            >
              Batal
            </button>
            &nbsp;
            <button
              tabIndex="2"
              className={this.state.aktif ? "btn btn-primary" : "btn btn-white"}
              disabled={this.props.isLoading}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </div>
        </div>
      </form>
    );
  }
}

FormModalKondisiPinjaman = reduxForm({
  form: "ModalKondisiPinjaman",
  enableReinitialize: true,
  validate: ValidasiMasterKondisiPinjaman,
})(FormModalKondisiPinjaman);
export default connect(maptostate, null)(FormModalKondisiPinjaman);
